<template>
  <transition name="slide-fade">
    <v-card elevation="7">
      <v-card-title>Offices </v-card-title>
      <v-card-text class="py-0">
        <v-timeline align-top dense>
          <v-timeline-item color="primary" small>
            <strong>Turkey</strong>
            <div class="text-caption mb-2">
              <center>
                <number
                  color="primary"
                  class="rte"
                  ref="countUser"
                  :from="0"
                  :to="offices.office_turkey"
                  :format="theFormat"
                  :duration="1"
                  :delay="1"
                  easing="Power1.easeOut"
                />
              </center>
            </div>
          </v-timeline-item>

          <v-timeline-item color="secondary" small>
            <strong>Syria</strong>
            <div class="text-caption">
              <div class="text-caption mb-2">
                <center>
                  <number
                    color="primary"
                    class="rte"
                    ref="countUser"
                    :from="0"
                    :to="offices.office_syria"
                    :format="theFormat"
                    :duration="1"
                    :delay="1"
                    easing="Power1.easeOut"
                  />
                </center>
              </div>
            </div>
          </v-timeline-item>
        </v-timeline>
      </v-card-text>
    </v-card>
  </transition>
</template>

<script>
import { dashboardController } from "./dashboard";

export default {
  data() {
    return {
      offices: {},
    };
  },
  mounted() {
    this.getOffices();
  },
  methods: {
    theFormat(number) {
      return number.toFixed(0);
    },
    getOffices: async function () {
      this.offices = await dashboardController.getOffices();
    },
  },
};
</script>

<style scoped>
.slide-fade-enter-active {
  transition: all 2s;
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.rte {
  font-size: 28px;
}
.slide-fade-enter 
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(-120px);
  opacity: 0;
}
</style>